import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';
import { loadDocs } from '../utils/loadDocs';
import TemplateA from '../templates/TemplateA';

const COMPONENTS = loadDocs();

class HomePage extends PureComponent {
  constructor() {
    super();
    COMPONENTS.forEach(component => {
      const splitName = component.default.name.split(/(?=[A-Z])/).join();
      this[splitName] = React.createRef();
    });
  }
  scrollToMyRef = ref => {
    window.scroll({ top: ref.current.offsetTop, behavior: 'smooth' });
  };

  render() {
    const sidebarContent = [];
    const content = [];

    COMPONENTS.forEach(component => {
      const { name } = component.default;
      const splitName = name.split(/(?=[A-Z])/);

      sidebarContent.push(
        <button
          className="btn"
          key={name}
          ref={this[splitName]}
          onClick={() => this.scrollToMyRef(this[splitName])}
        >
          {splitName.slice(0, -1).join(' ')}
        </button>,
      );

      content.push(
        <div ref={this[splitName]} key={name}>
          <Link key={name} to={splitName.join('-').toLowerCase()}>
            {splitName.slice(0, -1).join(' ')}
          </Link>
          <component.default />
        </div>,
      );
    });

    return <TemplateA content={content} sidebarContent={sidebarContent} />;
  }
}

export default HomePage;



