/* eslint-disable */
import React from 'react';
import Types from 'prop-types';
import Sidebar from '../regions/Sidebar';
import Content from '../regions/Content';
import Header from '../regions/Header';

const TemplateA = ({ sidebarContent, content }) => (
  <div className="container-fluid">
    <div className="row">
      <div className="col-xs-3 p-t-5">
        <Sidebar content={sidebarContent} />
      </div>
      <div className="col-xs-8">
        <Header />
        <Content content={content} />
      </div>
    </div>
  </div>
);

TemplateA.propTypes = {
  sidebarContent: Types.arrayOf(Types.shape({})).isRequired,
  content: Types.arrayOf(Types.shape({})).isRequired,
};

export default TemplateA;



